<template>
<div class="home-content">
<van-list
class="vant-list"
  finished-text="没有更多了"
  v-model="loading"
   @load="onLoad"
   :offset="10"
   :finished="finished"
>
	<home-item v-for="(item,index) in chatList" :key="index" :user="item" @click.native="itemClick(item)" :name="item.username"/>
</van-list>
</div>
	
</template>

<script>
import {List} from 'vant'
import HomeItem from './HomeItem'

export default {
  name: 'HomeContent',
  components:{
	HomeItem,
	[List.name]:List,
  },
  data(){
	return{
		loading:false,
		finished:false,
		refreshing:false,
		num:0,
		chatList:[
			{username:'小锐',message:'在吗？'},
			{username:'小强',message:'吃饭了没？'},
			{username:'牛大力',message:'能发一下验证码吗'},
			{username:'舒服真高',message:'别问了'},
			{username:'李青',message:'让我打野'},
			{username:'德莱文',message:'能来捉一波吗'},
			{username:'艾克',message:'反蹲一下'},
			{username:'蒙多',message:'游走吧'},
			{username:'破败之王',message:'别送我c'},
			{username:'锤石',message:'这AD TM会玩吗'},
			{username:'亚索',message:'看我秀死对面'},
		]
	}
  },
  methods:{
	onLoad(){
		console.log('加载')
		if(this.num>=3){
			this.finished = true;
			return 
		}
		this.num++
		setTimeout(()=>{
			let obj={username:'小锐',message:'[语音通话]'}
			this.chatList.push(obj)
			this.loading = false;
		},1000)
		
	},
	itemClick(item){
		item
		this.$router.push({name:'rundialog',params:{username:item.username}}).catch(err=>{err})
	},
  }
}
</script>

<style scoped>
.home-content{
	height:calc(100% - 99px);
	overflow:auto;
	position:relative;
}
</style>
